import React, { Component } from "react";
import { randomWord } from "./words";
import "./Hangman.css";
import img0 from "./img/0.png";
import img1 from "./img/1.png";
import img2 from "./img/2.png";
import img3 from "./img/3.png";
import img4 from "./img/4.png";
import img5 from "./img/5.png";
import img6 from "./img/6.png";

class Hangman extends Component {
  /** by default, allow 6 guesses and use provided gallows images. */
  static defaultProps = {
    maxWrong: 6,
    images: [img0, img1, img2, img3, img4, img5, img6]
  };

  constructor(props) {
    super(props);
    this.state = { nWrong: 0, guessed: new Set(), answer: randomWord() };
    this.handleGuess = this.handleGuess.bind(this);
    this.reset = this.reset.bind(this);
  }

  reset() {
    this.setState({
      nWrong: 0,
      guessed: new Set(),
      answer: randomWord()
    });
  }

  /** guessedWord: show current-state of word:
    if guessed letters are {a,p,e}, show "app_e" for "apple"
  */
  guessedWord() {
    return this.state.answer
      .split("") //map every letter generated by split ("" return an array with one single char)
      .map(ltr => (this.state.guessed.has(ltr) ? ltr : "_")); 

      //quando usuario clicar no botão, adicionará uma array dentro do set;
      //no começo, não existe nada dentro dela, então gera underscore do comprimento do método randomWord.
  }

  /** handleGuest: handle a guessed letter:
    - add to guessed letters
    - if not in answer, increase number-wrong guesses
  */
  handleGuess(evt) {
    let ltr = evt.target.value; //using event object to find what letter was clicked on
    this.setState(st => ({
      guessed: st.guessed.add(ltr), //adding that letter to set object
      nWrong: st.nWrong + (st.answer.includes(ltr) ? 0 : 1) //if the answer includes that letter
    }));
  }

  /** generateButtons: return array of letter buttons to render */
  generateButtons() {
    return "abcdefghijklmnopqrstuvwxyz".split("").map(ltr => (
      <button
        key={ltr}
        value={ltr}
        onClick={this.handleGuess}
        disabled={this.state.guessed.has(ltr)}
      >
        {ltr}
      </button>
    ));
  }

  /** render: render game */
  render() {
    const altText = `${this.state.nWrong}/${this.props.maxWrong} guesses`;
    const isWinner = this.guessedWord().join("") === this.state.answer;
    const gameOver = this.state.nWrong >= this.props.maxWrong;
    let gameState = this.generateButtons();
    if (isWinner) gameState = 'You Win!';
    if (gameOver) gameState = 'You Lose!';
    return (
      <div className='Hangman'>
        <h1>Hangman</h1>
        <img src={this.props.images[this.state.nWrong]} alt={altText} />
        <p>Guessed Wrong: {this.state.nWrong}</p>
        <p className='Hangman-word'>
        {!gameOver 
          ? this.guessedWord() 
          : this.state.answer}
        </p>
        <p className='Hangman-btns'>{gameState}</p>
        <button id='reset' onClick={this.reset}>
          Restart?
        </button>
      </div>
    );
  }
}

export default Hangman;